<template>
	<scroll-view scroll-y="true" :show-scrollbar="false" class="about">
		<view class="title">
			<view class="icon">蜜田</view>
		</view>
		<view class="content">
			<u-button 
				v-if="disabled"
				text="手机号一键登录" 
				color="#FE542C" 
				size="large" 
				open-type="getPhoneNumber" 
				class="login_button" 
				@getphonenumber="login"
			></u-button>
			<u-button v-else text="手机号一键登录" color="#FE542C" size="large" @click="setChecked"></u-button>
		</view>
		<view class="check-box">
			<u-checkbox-group>
				<u-checkbox 
					shape="circle" 
					activeColor="#FE542C" 
					v-model="checked"
					@change="changeCheck"
				></u-checkbox>
			</u-checkbox-group>
			<view style="color: #FE542C; font-size: 24rpx;">
				<text style="color: #999; margin-right: 6rpx;">我已阅读并同意</text>
				<text>蜜田用户服务协议、隐私政策</text>
			</view>
		</view>
	</scroll-view>
</template>

<script>
	import { loginWx } from '@/config/login.js';
	export default {
		data() {
			return {
				checked: false,
				disabled: false
			}
		},
				
		methods: {
			
			// change事件
			changeCheck(n) {
				this.disabled = n;
			},	
			
			// 时是否勾选用户协议
			setChecked() {
				if(!this.checked) return uni.showToast({ title: '请先勾选用户协议', icon: 'none' });
			},
			
			// 获取手机号登录
			login(e) {
				let detail = e.detail;
				console.log(detail, 'ssss')
				if (detail.errMsg == "getPhoneNumber:ok") {
				  let code = detail.code;
				  loginWx(code).then(res=> {
						if(res.success) {
							console.log(res, 'wx')
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '您已拒绝授权，请重新点击并授权!',
						showCancel: false,
					});
				}
			}
		}
	}
</script>

<style scoped lang="scss">
.about {
	color: #333;
	font-size: 28rpx;
	height: 100vh;
}
.title {
	height: 466rpx;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.icon {
	width: 181rpx;
	height: 182rpx;
	line-height: 182rpx;
	color: #fff;
	font-size: 70rpx;
	text-align: center;
	background: linear-gradient(69deg, #FF8972 0%, #FE5364 100%);
	border-radius: 47rpx;
}

.content {
	background-color: #fff;
	padding: 0 18rpx;
}
.check-box {
	padding-top: 70rpx;
	display: flex;
	align-items: center;
	justify-content: center;
}
</style>
